


















	var view;
	export default {
		data() {
			return {
				oldPw: '',
				newPw: '',
				rePw: ''
			};
		},
		onLoad() {
			view = this;
		},
		methods: {
			save() {
				if (!this.oldPw) {
					view.toast("请输入旧密码");
				} else if (!this.newPw) {
					view.toast("请输入新密码");
				} else if (!this.rePw) {
					view.toast("请确认密码");
				} else if (this.newPw !== this.rePw) {
					view.toast("两次输入的密码不一致");
				} else {

					view.$request({
						url: 'changePwd',
						showLoading: true,
						method:'POST',
						data: {
							"newPwd": this.newPw,
							"oldPwd": this.oldPw
						},
						success(res) {

						}
					})
				}
			}
		},
	}
